<template>
  <div class="search">
    <van-nav-bar class="border" title="搜索" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="border">
      <van-search
        v-model="value"
        @input="getAutoComplete"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
      >
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
    </div>
    <div v-if="autoCompleteData.length == 0">
      <div class="hot">
        <ul class="clearfix">
          <li
            v-for="(item,index) in hotWordsData.hotWords"
            :key="index"
            @click="searchList(item)"
          >{{item}}</li>
        </ul>
      </div>
      <div class="bg">
        <van-cell title="搜索历史">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <van-icon name="delete" @click="clearHistory" style="line-height: inherit;">删除历史</van-icon>
          </template>
        </van-cell>
      </div>
      <div>
        <van-cell
          class="bg"
          v-for="(item,index) in searchHistory"
          :key="index"
          :title="item"
          @click="searchList(item)"
        />
      </div>
    </div>
    <div v-else>
      <van-cell
        class="bg"
        v-for="(item,index) in autoCompleteData"
        :key="index"
        :title="item"
        @click="searchList(item)"
      />
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      arrData: [],
      index: 0,
      searchHistory: [],
      hotWordsData: [],
      autoCompleteData: []
    };
  },
  created() {
    let storage = window.localStorage;
    if (storage.getItem("searchHistory") !== null) {
      // this.historyLis 为渲染到页面
      this.searchHistory = JSON.parse(storage.getItem("searchHistory"));
    }
    this.getHotBooks();
  },
  methods: {
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    //点击搜索历史跳转到相应页面
    searchList(item) {
      this.value = item;
      this.onSearch();
    },
    //删除本地存储
    clearHistory() {
      localStorage.removeItem("searchHistory");
      this.searchHistory = [];
    },
    //添加搜索记录到本地存储
    onSearch() {
      if (this.value == "") {
        this.$toast("关键字不能为空");
      } else {
        let val = this.value;
        val = val.trim(); // 清除空格
        if (this.searchHistory.length > 0) {
          // 有数据的话 判断
          if (this.searchHistory.indexOf(val) !== -1) {
            // 有相同的，先删除 再添加
            this.searchHistory.splice(this.searchHistory.indexOf(val), 1);
            this.searchHistory.unshift(val);
          } else {
            // 没有相同的 添加
            this.searchHistory.unshift(val);
          }
        } else {
          // 没有数据 添加
          this.searchHistory.unshift(val);
        }
        if (this.searchHistory.length > 6) {
          // 保留六个值
          this.searchHistory.pop();
        }
        localStorage.setItem(
          "searchHistory",
          JSON.stringify(this.searchHistory)
        );
        this.$router.push({ name: "Classify", query: { val: this.value } });
      }
    },
    getHotBooks() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/hot-books"
      })
        .then(result => {
          this.$toast.clear();
          
          if (Object.keys(result.data).length > 0) {
            this.hotWordsData = result.data;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    getAutoComplete() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/auto-complete?query=" + this.value
      })
        .then(result => {
          this.$toast.clear();
          
          this.autoCompleteData = result.data.keywords;
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

