.search {
  /deep/ .van-icon-delete::before {
    margin-right: 3px;
  }
  .border {
    margin: 0px 0 10px;
  }
  /deep/ .bg:not(:last-child) {
    border-bottom: 1px solid #ccc;
  }
  .hot {
    padding: 0 5px;
    margin-bottom: 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        padding: 8px 10px;
        border: 1px solid #ccc;
        margin: 5px;
        border-radius: 10px;
        font-size: 16px;
      }
    }
  }
}
</style>